import React from "react"
import Layout2 from "../components/layout2"
import SEO from "../components/seo"
import Footer from "../components/footer"

import Waves from "../images/wave.png"

const Lore = () => {
  const charms = [5, 5, 5, 5, 5]

  function genCharmCard(charm, i) {
    return <div className="card" key={i}>x</div>
  }

  return (
    <>
      <Layout2>
        <SEO title="lore" />
        <div className="charmBack">
          <img className="water" src={Waves} width="664" height="564" alt="water" />
          <img className="fire" src={Waves} width="664" height="564" alt="fire" />
        </div>
        <div className="charmGrid">
          {charms.map((charm, i) => genCharmCard(charm, i))}
        </div>
        <div className="heroText">
          <h3> Wiki </h3>
          <p> The elements, at your fingertips </p>
        </div>
        <div className="charmGrid">
          {charms.map((charm, i) => genCharmCard(charm, i))}
        </div>
      </Layout2>
      <div className="pageContent">
        <div className="row">
          Lore goes here.
        </div>
      </div>
      <Footer />
    </>
  )
}

export default Lore